<template>
  <div>
        <el-form ref="addForm" :rules="rules" :model="dataForm" label-width="120px" class="commonsty-form">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="列名" prop="colCode">
                        <el-input type="text" v-model="dataForm.colCode" maxlength='15' placeholder="请输入列名" style="width:100%"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="长度" prop="colDatalength">
                      <el-input type="text" v-model="dataForm.colDatalength" maxlength='15' placeholder="请输入长度" style="width:100%"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                 <el-col :span="12">
                    <el-form-item label="中文名" prop="colName">
                        <el-input type="text" v-model="dataForm.colName" maxlength='10' placeholder="请输入中文名" style="width:100%"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="数据类型" prop="colDatatype">
                        <el-select v-model="dataForm.colDatatype" placeholder="请选择数据类型" style="width:100%" @change='selectChange'>
                            <el-option  v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                 <el-col :span="12">
                    <el-form-item label="默认值" prop="colDefaultvalue">
                        <el-input type="text" v-model="dataForm.colDefaultvalue" maxlength='10' placeholder="请输入默认值" style="width:100%"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="显示格式" prop="colDateformat" v-if="isShow1">
                        <el-select v-model="dataForm.colDateformat" placeholder="请选择" style="width:100%">
                            <el-option  v-for="item in dateFormatList" :key="item.name" :label="item.name" :value="item.name"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="关联字典" prop="colDiccode"  v-show="isShow2">
                        <el-select v-model="dataForm.colDiccode" placeholder="请选择" style="width:100%">
                            <el-option  v-for="item in dataDictList" :key="item.dictName" :label="item.dictName" :value="item.dictName"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="是否主键" prop="colIspk">
                        <el-radio-group v-model="dataForm.colIspk">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="是否必填" prop="colIspk">
                        <el-radio-group v-model="dataForm.colIsnotnull">
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="是否计算列" prop="colIscalculate">
                        <el-radio-group v-model="dataForm.colIscalculate" @change='radioChange'>
                            <el-radio :label="1">是</el-radio>
                            <el-radio :label="0">否</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="计算函数" prop="funId">
                        <el-select v-model="dataForm.funId" placeholder="请选择" :disabled=disabled style="width:100%">
                            <el-option  v-for="item in calcuFunDictList" :key="item.dictName" :label="item.funName" :value="item.funId"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <el-form-item label="表单跨列" prop="colSpan">
                  <el-input-number v-model="dataForm.colSpan"  controls-position="right" :min="8" :max="24" label="描述文字" style="width:100%"></el-input-number>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="表单文本域高度" prop="rowsNum">
                  <el-input-number v-model="dataForm.rowsNum"  controls-position="right" :min="1" :max="500" label="描述文字" style="width:100%"></el-input-number>
                </el-form-item>
              </el-col>
            </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="列表宽度" prop="width">
                <el-input-number  v-model="dataForm.width"  controls-position="right" :min="1" :max="500" label="描述文字" style="width:100%"></el-input-number>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="列表对齐方式" prop="align">
                <el-select v-model="dataForm.align"  style="width:100%" @change='selectChange'>
                  <el-option  v-for="item in tableAlignOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="关联字典" prop="dicCode">
                <el-select v-model="dataForm.dicCode"  style="width:100%" @change='selectChange'>
                  <el-option  v-for="item in dicCodeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="控件类型" prop="controlType">
                <el-select v-model="dataForm.controlType"  style="width:100%" @change='selectChange'>
                  <el-option  v-for="item in controlTypeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
           </el-row>
            <el-row>
              <el-col :span="12">
                <el-form-item label="显示方式" prop="isShowAtForm">
                  <el-select v-model="dataForm.isShowAtForm"  style="width:100%" @change='selectChange'>
                    <el-option  v-for="item in showOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="可否编辑" prop="isEdit">
                  <el-select v-model="dataForm.isEdit"  style="width:100%" @change='selectChange'>
                    <el-option  v-for="item in yesnoOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
          </el-row>
            <div class="dialog-footer">
                <el-button type="primary" @click="saveArea()">保存</el-button>
                <el-button type="danger" @click="cancle()">取消</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default{
        props: [
            'checkedData',
            'isEdit',//true：添加，false 编辑
            'currentRow'
        ],
        data(){
            return{
                //表单验证
                rules: {
                    colCode: [{
                        required: true,
                        message: '请输入列名',
                        trigger: 'change'
                    }],
                    colName: [{
                        required: true,
                        message: '请输入中文名',
                        trigger: 'change'
                    }],
                    colDatalength: [{
                      required: true,
                      message: '请输入长度',
                      trigger: 'change'
                    }],
                    colDatatype: [{
                      required: true,
                      message: '请输入数据类型',
                      trigger: 'change'
                    }],
                },
                options: [],
                dateFormatList:'',
                dataDictList:'',
                calcuFunDictList:[],
                isShow1:false,
                isShow2:false,
                disabled:false,
                dataForm:{
                    colId:'',
                    tabId:'',
                    colCode:'',
                    colName:'',
                    colDatatype:'',
                    colDatalength:'',
                    colIspk:1,
                    colIsnotnull:1,
                    colDateformat:'',
                    colDiccode:'',
                    funId:'',
                    colDefaultvalue:'',
                    colIscalculate:0, //是否是计算列1是 0否
                    colSpan:'',
                    rowsNum:'',
                    width:'',
                    align:'',
                    dicCode:'',
                    controlType:'',
                    isShowAtForm:'',
                    isEdit:'',
                },
                dicCodeOptions:[],
                controlTypeOptions:[
                  {
                    value:'3',
                    label:'时间选择框'
                  },
                  {
                    value:'4',
                    label:'字典选择框'
                  },
                  {
                    value:'13',
                    label:'机构选择框'
                  },
                  {
                    value:'2',
                    label:'多行文本框'
                  },
                  {
                    value:'1',
                    label:'单行文本框'
                  },
                  {
                    value:'15',
                    label:'年度多选型'
                  },
                  {
                    value: "8",
                    label: "数字文本框",
                  },
                  {
                    value: "5",
                    label: "下拉框类型",
                  },
                  {
                    value: "6",
                    label: "单选框类型",
                  },
                  {
                    value: "7",
                    label: "复选框类型",
                  }
                ],
                  yesnoOptions:[
                    {
                      value:'0',
                      label:'是'
                    },
                    {
                      value:'1',
                      label:'否'
                    }
                  ],
                  tableAlignOptions:[
                    {
                      value:'0',
                      label:'左对齐'
                    },
                    {
                      value:'1',
                      label:'居中'
                    },
                    {
                      value:'2',
                      label:'右对齐'
                    },
                  ],
                  showOptions:[
                    {
                      value:'0',
                      label:'列表中显示'
                    },
                    {
                      value:'1',
                      label:'表单中显示'
                    },
                    {
                      value:'3',
                      label:'通用'
                    },
                    {
                      value:'2',
                      label:'隐藏'
                    },
                  ],
            }
        },
        created(){
           console.log(this.checkedData)
          console.log(typeof  this.checkedData.rowsNum)
            this.setFormData();
            this.getDictItem()
            //获取计算函数
            this.getDataDictList()
           //获取关联字典
            this.getDictionary()
        },
        methods:{
            getDictItem(){
              //获取字典项
              let params = {
                dictCode:'DataType'
              }
              this.$http({
                url: this.$http.adornUrl(this.API.getDictItemTable),
                method: 'post',
                data: this.$http.adornData(
                  params
                )
              }).then((res) =>{
                var resData = res.data;
                if(resData.code == 0) {
                  let dictData = resData.data
                  let optionsArr = []
                  dictData.forEach(function (value,index) {
                    let obj = {
                      value:value.itemCode,
                      label:value.itemName,
                    }
                    optionsArr.push(obj)
                  });
                  this.options = optionsArr
                } else {
                  this.$message.error(resData.msg);
                }
              })
            },
            setFormData(){

              console.log(this.checkedData)
                if(this.isEdit === false){

                    this.dataForm.colId = this.checkedData.colId
                    this.dataForm.colCode = this.checkedData.colCode
                    this.dataForm.colName = this.checkedData.colName
                    this.dataForm.colDatatype = this.checkedData.colDatatype
                    this.dataForm.colDatalength = this.checkedData.colDatalength
                    this.dataForm.colIspk = Number(this.checkedData.colIspk)  //是否主键 1是0否
                    this.dataForm.colIsnotnull = Number(this.checkedData.colIsnotnull)  //是否必填 0为空 1不为空
                    this.dataForm.colDateformat = this.checkedData.colDateformat //显示格式
                    this.dataForm.colDiccode = this.checkedData.colDiccode //关联字典
                    this.dataForm.colDefaultvalue = this.checkedData.colDefaultvalue //默认值
                    this.dataForm.colIscalculate = Number(this.checkedData.colIscalculate) //是否是计算列1是 0否
                    this.dataForm.funId = this.checkedData.funId

                    this.dataForm.colSpan = this.checkedData.colSpan
                    this.dataForm.rowsNum = this.checkedData.rowsNum
                    this.dataForm.width = this.checkedData.width
                    this.dataForm.align = this.checkedData.align
                    this.dataForm.dicCode = this.checkedData.dicCode
                    this.dataForm.controlType = this.checkedData.controlType
                    this.dataForm.isShowAtForm = this.checkedData.isShowAtForm
                    this.dataForm.isEdit = this.checkedData.isEdit

                  console.log(this.checkedData)

                    if(this.checkedData.colIscalculate == "1"){
                        this.disabled = false;
                    }else{
                        this.disabled = true;
                    }
                    if(this.checkedData.colDateformat === null || this.checkedData.colDateformat === ''){
                        this.isShow1=false;
                    }else{
                        this.isShow1=true;
                        this.getDateFormat();
                    }
                    if(this.checkedData.colDiccode === null || this.checkedData.colDiccode === ''){
                        this.isShow2=false;
                    }else{
                        this.isShow2=true;
                        this.getDataDictList();
                    }
                }else{
                  if(this.checkedData.colIscalculate == "1"){
                    this.disabled = false;
                  }else{
                    this.disabled = true;
                  }
                  this.dataForm.tabId = this.checkedData.id
                }
                console.log(this.dataForm)
            },

            selectChange(e){
              //判断是否关联字典项
              this.options.find((item)=>{
                if(item.value === e){//筛选出匹配数据

                  if(item.label == '日期型'){
                    this.isShow1=true;
                    this.getDateFormat();
                  }else{
                    this.isShow1=false;
                  }
                  if(item.label =='字典型'){
                    this.isShow2=true;
                    this.getDataDictList();
                  }else{
                    this.isShow2=false;
                  }

                }
              })
            },
            radioChange(e){
                if( e == 0){
                    this.disabled = true;
                }else{
                    this.disabled = false;
                }
            },
            //日期格式
            getDateFormat(){
                this.$http({
                    url:this.$http.adornUrl(this.API.getDictItemTable),
                    method:'post',
                    data:this.$http.adornData({
                        dictCode:'DateFormat'
                    })
                }).then(({data}) => {
                    if(data.code == 0){
                        this.dateFormatList = data.data.data;
                    } else {
                        this.$message.error(data.meg)
                    }
                })
            },
            //字典类型
            getDataDictList(){
                this.$http({
                    url:this.$http.adornUrl(this.API.getDataDictListDict),
                    method:'post',
                    data:this.$http.adornData({

                    })
                }).then(({data}) => {
                    if(data.code == 0){
                        this.dataDictList = data.data.records;
                    } else {
                        this.$message.error(data.meg)
                    }
                })
            },
            //关联函数字典
            getDataDictList(){
              this.$http({
                url:this.$http.adornUrl(this.API.queryDataFunction),
                method:'post',
                data:this.$http.adornData({

                })
              }).then(({data}) => {
                console.log(data)
                if(data.code == 0){
                  this.calcuFunDictList = data.data
                } else {
                  this.$message.error(data.meg)
                }
              })
            },
            //获取关联字典
            getDictionary(){
              this.$http({
                url:this.$http.adornUrl(this.API.getDictInformation),
                method:'post',
              }).then(({data}) => {
                console.log(data)
                if(data.code == 0){
                  let dictionaryArr = []
                  let dictionaryData = data.data
                  dictionaryData.forEach(function(item,index){
                    let obj = {
                      value:item.dictCode,
                      label:item.dictName
                    }
                    dictionaryArr.push(obj)
                  })
                  this.dicCodeOptions = dictionaryArr
                } else {
                  this.$message.error(data.meg)
                }
              })
            },
            //保存
            saveArea(){
                this.$refs['addForm'].validate((valid) => {
                    console.log(this.currentRow)
                  console.log(this.checkedData)
                    this.dataForm.tabId = this.checkedData.id
                    if(valid) {
                      if(this.isEdit){
                        // 添加
                        this.$http({
                          url: this.$http.adornUrl(this.API.savaDataTableColumn),
                          method: 'post',
                          data: this.$http.adornData(
                            this.dataForm
                          )
                        }).then((res) =>{
                          var resData = res.data;
                          if(resData.code == 0) {
                            this.$message({
                              message: '成功',
                              type: 'success'
                            });
                            this.$emit("addback",true);
                          } else {
                            this.$message.error(resData.msg);
                          }
                        })

                      }else{
                        //编辑
                        this.dataForm.tabId = this.currentRow.id
                        this.$http({
                          url: this.$http.adornUrl(this.API.updateDataTableColumn),
                          method: 'post',
                          data: this.$http.adornData(
                            this.dataForm
                          )
                        }).then((res) =>{
                          var resData = res.data;
                          if(resData.code == 0) {
                            this.$message({
                              message: '成功',
                              type: 'success'
                            });
                            this.$emit("addback",true);
                          } else {
                            this.$message.error(resData.msg);
                          }
                        })
                      }




                        // this.$http({
                        //     url: this.$http.adornUrl(this.API.savaOrUpdateColumn),
                        //     method: 'post',
                        //     data: this.$http.adornData(
                        //         this.dataForm
                        //     )
                        // }).then((res) =>{
                        //     var resData = res.data;
                        //     if(resData.code == 0) {
                        //         this.$message({
                        //             message: '成功',
                        //             type: 'success'
                        //         });
                        //         this.$emit("addback",true);
                        //     } else {
                        //         this.$message.error(resData.msg);
                        //     }
                        // })
                    }
                })
            },
            //取消
            cancle() {
                this.$emit("addback",false);
            }
        },
    }
</script>

<style>
  .colsty > .el-col-12{
    padding: 0 4px;
  }
</style>



